* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 1.5rem;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.column {
  flex-direction: column;
}

html {
  font-size: 10px;
}

body {
  width: 100%;
  height: 100vh;
  background-color: #3D405B;
  position: relative;
}

button {
  margin: 0 5rem;
  height: 50px;
  padding: 5px 35px;
  cursor: pointer;
  border: none;
  font-size: 17px;    
  background-color: #fad688;
  font-weight: bolder;
  border: 1px solid black;
}

.contGen {
  height: 100vh;
  width: 100%;
}

input {
  padding: 1rem;
  margin: 0.5rem 0;
}

h1 {
  color: white;
  font-size: 4rem;
}

.swal2-popup {
  font-size: 1.6rem !important;
}